import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Card } from './Card'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

export const Cards = () => {
  const MySwal = withReactContent(Swal)
  const [rickApiRes, setApiRes] = useState([])
  useEffect(() => {
    getData()
  }, [])

  const showModal = async (name, species, origin, location, gender) => {
    await MySwal.fire({
      title: <strong>{name}</strong>,
      html: (
        <RickModal>
          <li>Origin: </li><p>{origin}</p>
          <li>Species: </li> <p>{species}</p>
          <li>Location: </li> <p>{location}</p>
          <li>Gender: </li> <p>{gender}</p>

        </RickModal>
      ),
    })
  }

  const getData = async () => {
    let resp = await fetch('https://rickandmortyapi.com/api/character')
    let data = await resp.json()
    setApiRes(data.results)
  }
  return (
    <Div>
      {rickApiRes.map((json, index) => (
        <Card
          showModal={showModal}
          key={index}
          name={json.name}
          img={json.image}
          species={json.species}
          origin={json.origin.name}
          location={json.location.name}
          gender={json.gender}
        />
      ))}
    </Div>
  )
}

const Div = styled.div`
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 2rem;
  /* column-gap: 2rem; */
  align-items: center;
  justify-content: center;
  @media(min-width: 640px){
    grid-template-columns: repeat(3, 1fr);
  }
  @media(min-width: 768px){
    grid-template-columns: repeat(4, 1fr);
  }
`

const RickModal = styled.div `
display: grid;
grid-template-columns: repeat(2, 1fr);
align-items: center;
list-style: none;
li{
  font-weight: bold;
}
`