import React, {useState} from 'react'
import styled from 'styled-components'

export const Card = ({ name, img, showModal, species, origin, location, gender}) => {
  return (
    <CardCont onClick={() => showModal(name, species, origin, location, gender) }>
      <div className="imgRelative">
        <img src={img} alt={name} />
        <span>Click for more info</span>
      </div>
      <h4>{name}</h4>
    </CardCont>
  )
}

const CardCont = styled.div`
  margin: 0 auto;
  width: 10rem;
  text-align: center;
  color: white;
  background-color: #19415c;
  box-shadow: 1px 2px 5px #222;

  border-radius: 0.5rem;
  transition: all 0.15s ease-in;

  .imgRelative {
    img {
      width: 10rem;
      border-radius: 0.3rem;
      position: relative;
      transition: all .2s ease;
    }
    span {
      display: none;
    }
  }
  &:hover {
    transform: scale(1.12);
    box-shadow: 0 2px 20px #acd34b;
    cursor: pointer;
    .imgRelative {
      img {
        filter: brightness(0.3);
      }
      span {
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
      }
    }
  }
`
