import styled from 'styled-components'

export const Footer = () => {
  return (
    <Pie>
      <div>
      <h2><a target="_blank" href="https://joakov3.github.io">Joaquin Varela</a></h2>   
        <span>Software Developer <code>&lt;/&gt;</code> </span>
      </div>
      <div className="info">
        <h4>Info about this project:</h4>
        <p>This is a front-end project made for myself and to demonstrate my work with API's <br/> 
          I am using 'rickandmortyapi.com' and it is a great tool for this matter 
        </p>
      </div>
    </Pie>
  )
}

const Pie = styled.div`
  background-color: #222;
  min-height: 20vh;
  margin: 0 auto;
  padding: 2rem;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  h2{
    margin: 0;
  }
  h2 a{
    color: #fff;
    transition: all .3s ease-in-out;
    text-decoration: none;
  }
  h2 a:hover{
    color: #1a82a5;
  }
  div{
    max-width: 30rem;
    margin: 0 auto;
    text-align: center;
  }
  .info{
    text-align: justify;
  }
  div h4{
    font-size: 1.3rem;
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 0;
  }
` 


